@import url(https://fonts.googleapis.com/css?family=Cairo);
@import url(https://fonts.googleapis.com/css?family=Abel);
@import url(https://fonts.googleapis.com/css?family=Quicksand:300);
@import url(https://fonts.googleapis.com/css?family=Quicksand:300);
@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=ZCOOL+QingKe+HuangYou);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }

.abouttitle {
  color: dark;
  padding-top: 10vh;
  font-family: "Cairo", sans-serif;
  text-align: center;
  font-size: 30px;
  line-height: 70%; }

.containertext {
  padding: 50px; }
  .containertext h3 {
    font-family: "Cairo", sans-serif;
    text-align: center;
    font-size: 30px;
    color: dark; }
  .containertext h4 {
    font-size: 30px;
    font-family: "Abel", sans-serif;
    color: dark; }

.title {
  width: 20vw; }

.picture {
  margin: 20px; }

.iconquest {
  width: 80px; }

.landingtext {
  color: lightgray;
  padding-top: 5vh;
  font-family: "Quicksand", sans-serif;
  text-align: center;
  font-size: 20px;
  letter-spacing: 3px; }

.fixednav {
  position: fixed; }

.navbar-item {
  letter-spacing: 3px;
  background-color: dark;
  color: lightgray;
  font-family: "Quicksand", sans-serif; }

.module {
  scroll-behavior: auto; }

.is-back {
  background: rgba(0, 0, 0, 0.6); }

.logospace {
  margin: 5px; }

.landing {
  height: 100vh;
  background: url(/react-portafolio/static/media/portafolionew.f367dd1a.png) center;
  background-size: 60vw;
  -webkit-animation: bg-animationl linear 40s infinite;
          animation: bg-animationl linear 40s infinite; }

@-webkit-keyframes bg-animationl {
  from {
    background-position: 0% 0px; }
  to {
    background-position: 100% 0px; } }

@keyframes bg-animationl {
  from {
    background-position: 0% 0px; }
  to {
    background-position: 100% 0px; } }

.shadow {
  background: -moz-radial-gradient(center, ellipse cover, rgba(0, 0, 0, 0) 0%, black 100%); }

.header {
  position: relative;
  float: left;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }
  .header h3 {
    font-size: 30px;
    font-family: "Lato", sans-serif;
    color: white; }
  .header h4 {
    font-size: 15px;
    font-family: "Lato", sans-serif;
    color: white; }

.cls-1 {
  fill: #fff; }

.cls-2 {
  fill: #f70c5b; }

.backout {
  background: linear-gradient(lightgray 0%, white, white 100%);
  background-size: 100%; }

.title {
  font-family: "Oswald", sans-serif;
  text-align: right;
  font-size: 35px;
  color: dark; }

.infotext {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  font-size: 17px;
  color: gray; }

.content {
  padding-top: 100px; }

.background {
  width: 100%; }

.info {
  padding: 20px; }

.car {
  width: 40%; }

.line {
  background-color: #91334B;
  height: 5px; }

.linew {
  background-color: white;
  height: 5px; }

.react-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  pointer-events: none; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.personal {
  background: url(/react-portafolio/static/media/portafolioinfo.592e1876.png) center;
  background-size: 60vw;
  -webkit-animation: bg-animation linear 50s infinite;
          animation: bg-animation linear 50s infinite; }

@-webkit-keyframes bg-animation {
  from {
    background-position: 0% 0px; }
  to {
    background-position: -200% 0px; } }

@keyframes bg-animation {
  from {
    background-position: 0% 0px; }
  to {
    background-position: -200% 0px; } }

.info {
  font-size: 20px;
  color: white;
  font-family: "Raleway", sans-serif; }

.bold {
  font-size: 20px;
  color: white;
  font-family: "ZCOOL QingKe HuangYou", cursive; }

.infocont {
  background-color: rgba(255, 255, 255, 0.2); }

.backgroundskill {
  background-color: #f9f9f9; }

